<template>
  <div class="mansinfo">
    <div class="mansinfo-bg">
    </div>
    <div class="mansinfo-avatar clearfix">
      <div class="mansinfo-avatar-img ">
        <el-avatar :size="96"  src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
      </div>
      <div class="mansinfo-info">
        <div style="color: #FFFFFF;font-size: 1.5rem;">
          <span>oqv</span>
        </div>
        <div style="font-size: 12px;line-height: 24px;">
          <div>
            <span>QQ:{{oneManInfo.account}}</span>
          </div>
          <div>
            <span>{{oneManInfo.sex}}</span>
            <span>{{oneManInfo.constellation}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="mansinfo-body">
      <div class="mansinfo-body-sign">
        <i class="el-icon-edit"></i>
        <span style="margin-left: 1rem;">{{oneManInfo.signature}}</span>
      </div>
      <div class="mansinfo-body-btn">
        <el-button >发送消息</el-button>
        <el-button type="primary" >添加好友</el-button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      oneManInfo: {
        account: '',
        sex: '',
        constellation: '',
        signature: ''
      }

    }
  },
  mounted () {
    this.getAccountInfo()
  },
  methods: {
    getAccountInfo () {
      const num = this.$route.params.account
      if (num) {
        this.axios({
          url: '/user/maninfo',
          method: 'post',
          params: {
            account: num
          }
        }).then(resp => {
          if (resp.data.status === 200) {
            this.oneManInfo = resp.data.data
          }
          if (resp.data.status === 400) {
            this.$message({
              type: 'warning',
              message: '请求失败了哦⚪.⚪!'
            })
          }
        }).catch(resp => {
          this.$message.error('哎呀！报错了0.0')
        })
      }
    }
  }
}
</script>

<style>
  .mansinfo{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .mansinfo-bg{
    width: 100%;
    height: 40%;
    background-image:url(../assets/maninfo-bg.jpg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    z-index: 1;
  }
  .mansinfo-avatar{
    height: 6rem;
    width: calc(100% - 6rem);
    position: relative;
    bottom:3rem;
    left: 2rem;
    position: relative;
  }
  .mansinfo-avatar-img{
    width: 6rem;
    height: 100%;
    border: 2px solid #FFFFFF;
    border-radius: 50%;
    background-color: #FFFFFF;
    position: absolute;
    z-index: 1000;
  }
  .mansinfo-info{
    height: 100%;
    position: absolute;
    float: left;
    left: 7.5rem;
    z-index: 1000;
  }
  .mansinfo-info div{
    width: 100%;
    height: 50%;
  }
  .mansinfo-body{
    width: 100%;
    position: relative;
    bottom: 2rem;
  }
  .mansinfo-body>div{
    margin-left: 2rem;
  }
  .mansinfo-body-btn{
    position: fixed;
    width: 100%;
    bottom: 0;
    margin-bottom: 1rem;
    padding-top: 0.5rem;
    text-align: center;
    margin-left: 0 !important;
    border-top: 1px solid #F0F0F0;
  }
</style>
