<template>
  <div class="findmansorgroups">
      <div class="findmansorgroups-content clearfix">

          <div style="width: 100%;position: absolute;4rem" >
            <div class="findmansorgroups-content-jn clearfix">
              <div>
                <div style="width: 50%;float: left;height: 100%;" @click="addClo=true" :class="{findmansorgroups_addclo:addClo}">
                  <span>找人</span>
                </div>
                <div  style="width: 50%;float: left;height: 100%;"  @click="addClo=false" :class="{findmansorgroups_addclo:!addClo}">
                  <span>找群</span>
                </div>
              </div>
            </div>
          </div>
          <div style="font-size: 1.5rem;width: 10%;text-align: center;position: absolute;z-index: 100;">
              <i class="el-icon-arrow-left" @click="back"></i>
          </div>
      </div>
      <div class="findmansorgroups-body">
        <div class="findmansorgroups-search">
          <div class="findmansorgroups-lagin clearfix">
            <div style="float: left;">
              <i class="el-icon-search"></i>
            </div>
            <div style="float: left;width: 80%;">
              <input placeholder="QQ号/昵称" v-show="addClo" class="findmansorgroups-input" @keyup.enter="findmans('friends')" v-model="value" />
              <input placeholder="群号/群名称" v-show="!addClo" class="findmansorgroups-input" @keyup.enter="findmans('groups')" v-model="value"/>
            </div>
          </div>
        </div>
        <div class="findmansorgroups-manslist">
          <div class="findmansorgroups-manslist-items" v-for="(man,index) in manslist" :key="index">
            <router-link :to="'/user/maninfo/'+man.account" style="line-height:4rem ;width: 100%;height:80%;position: relative;top:10%;display: block;">
              <div style="height: 100%;float: left;margin-left: 0.5rem;">
                <el-avatar :size="50" :src="'data:image/*;base64,'+man.avatar"></el-avatar>
              </div>
              <div style="float: left;width: 20rem;height: 100%;margin-left:0.5rem;" class="clearfix">
                <div style="height: 50%;width: 100%;;line-height: 2rem;">
                  <span style="color:#409EFF ;">{{man.nickname}}</span>
                  <span style="color: #606266;">( {{man.account}} )</span>
                </div>
                <div style="height: 50%;width: 100%;;line-height: 2em;">
                  <span ><el-tag color="#22E3FC" style="color: #FFFFFF;" size="mini">{{man.birthday}}</el-tag></span>
                  </div>
              </div>
            </router-link>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  methods: {
    back () {
      this.$router.go(-1)
    },
    findmans (type) {
      const val = this.value
      if (val) {
        this.axios({
          url: '/user/add' + type,
          method: 'post',
          params: {
            param: val
          }
        }).then(resp => {
          if (resp.data.status === 200) {
            this.manslist = resp.data.data
          }
          if (resp.data.status === 400) {
            this.$message({
              type: 'warning',
              message: '请求失败了哦--|!'
            })
          }
        }).catch(resp => {
          this.$message.error('哎呀！服务器报错了0.0')
        })
      } else {
        this.$message({
          type: 'warning',
          message: '请输入关键字'
        })
      }
    }
  },
  data () {
    return {
      addClo: true,
      value: '',
      manslist: null

    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo
    })
  }

}
</script>

<style>
  .findmansorgroups{
    width: 100%;
    height: 100%;
  }
  .findmansorgroups-content{
    width: 100%;
    height: 4rem;
    background-image: linear-gradient(to right, #26E7FD, #13D6FB);
    position: relative;
  }

  .findmansorgroups-content>div{
    float: left;
    line-height: 4rem;
    height: 100%;
    color: #FFFFFF;
  }

  .findmansorgroups-content-jn{
    width: 50%;
    height: 100%;
    line-height: 4rem;
    text-align: center;
    margin: 0 auto;

  }
  .findmansorgroups-content-jn>div{
    width: 70%;
    margin: 0 auto;
    height: 70%;
    position: relative;
    top:15%;
    border: 1px solid;
    border-color: #FFFFFF;
    border-radius: 50px;
    line-height: 40px;
    overflow: hidden;
  }
  .findmansorgroups-content-jn>div span{
    vertical-align: middle;
  }
  .findmansorgroups-content-jn>div span:first-child{
    margin-right: 8px;
  }
  .findmansorgroups-content-jn>div span:last-child{
    margin-left: 8px;
  }
  .findmansorgroups_addclo{
    background-color: #FFFFFF;
    color: #1DDFFC;
  }
  .findmansorgroups-search{
    height: 4rem;
    width: 100%;
    background-color: #FFFFFF;
    position: relative;
    top:12px;
    line-height: 4rem;
    font-size: 1.2rem;
    color: #C0C4CC;
  }
  .findmansorgroups-body{
    width: 100%;
    height:calc(100% - 4rem);
    background-color: #F2F6FC;
    position: relative;
  }
  .findmansorgroups-lagin{
    width: 90%;
    height: 100%;
    margin: 0 auto;
  }
  .findmansorgroups-manslist{
    height: calc(100% - 8rem);
    width: 100%;
    position: relative;
    top: 24px;
    background-color: #FFFFFF;
    overflow: auto;
  }
  .findmansorgroups-input{
    border: 0;
    text-indent: 1rem;
    letter-spacing: 3px;
    display: block;
    height:4rem;
    width: 100%;
    outline: none;
  }
  .findmansorgroups-manslist-items{
    height: 4rem;
    width: 100%;
    position: relative;
    border-top: 0.1px solid #F2F6FC;
  }
</style>
