import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

/**
 * 相关页面路由
 *
 */
import Subject from '../views/subject.vue'
import Login from '../components/login.vue'
import Register from '../components/register.vue'
import UserMessage from '../views/messages.vue'
import LinkMans from '../views/linkmans.vue'
import AccountNum from '../components/accountnum.vue'
import Friends from '../components/linkmans/friendsgroup.vue'
import GroupsChat from '../components/linkmans/groupschat.vue'
import FindMansOrGroups from '../views/findmansorgroup.vue'
import ManInfo from '../views/maninfo.vue'

const routes = [
  {
    name: 'subject',
    path: '/',
    component: Subject,
    redirect:'/usermessages',
    children:[
      {
        path:'usermessages',
        name:'usermessages',
        component:UserMessage
      },
      {
        path:'linkmans',
        name:'linkmans',
        component:LinkMans,
        redirect:'/linkmans/friends',
        children:[
          {
            path:'friends',
            name:'friends',
            component:Friends
          },
          {
            path:'groupschat',
            name:'groupschat',
            component:GroupsChat
          }

        ]
      }
    ]
 },
 {
   name:'login',
   path:'/login',
   component:Login
 },
 {
   name:'register',
   path:'/register',
   component:Register
 },
 {
   name:'accountnum',
   path:'/accountnum',
   component:AccountNum
 },
 {
   name:'findmansorgroups',
   path:'/findmansorgroups',
   component:FindMansOrGroups
 },
 {
   name:'maninfo',
   path:'/user/maninfo/:account',
   component:ManInfo
 }
]

const router = new VueRouter({
  routes
})

export default router
